@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.coin-container {
	display: flex;
	justify-content: center;
}

.coin-row {
	display: flex;
	justify-content: flex-start;
	align-items: center;
	height: 80px;
	border-bottom: 1px solid #d7d7d7;
	width: 900px;
}

.coin {
	display: flex;
	align-items: center;
	padding-right: 24px;
	min-width: 300px;
}

.coin img {
	height: 30px;
	width: 30px;
	margin-right: 10px;
}

.coin h1 {
	font-size: 16px;
	width: 150px;
}

.coin-symbol {
	text-transform: uppercase;
}

.coin-data {
	display: flex;
	text-align: center;
	justify-content: space-between;
	width: 100%;
}

.coin-price {
	width: 110px;
	margin-right: 20px;
}

.coin-volume {
	width: 230px;
}

.coin-marketcap {
	width: 155px;
}

.coin-percent {
	width: 100%;
}
.red {
	color: #f00606;
}
.green {
	color: #11d811;
}

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	font-family: 'Montserrat', sans-serif;
	background: #1a1a1c;
	color: #fff;
}

.coin-app {
	display: flex;
	flex-direction: column;
	align-items: center;
	margin-top: 64px;
	color: #fff;
}

.coin__search {
	margin-bottom: 64px;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.coin--text {
	margin-bottom: 32px;
	text-align: center;
}

.coin__search--input {
	padding-left: 16px;
	width: 300px;
	height: 50px;
	border-radius: 4px;
	border: none;
	background: linear-gradient(-225deg, #ac32e4 0%, #7918f2 48%, #4801ff 100%);
	color: #e2e2e2;
}

.coin__search--input::-webkit-input-placeholder {
	color: #e2e2e2;
}

.coin__search--input:-ms-input-placeholder {
	color: #e2e2e2;
}

.coin__search--input::placeholder {
	color: #e2e2e2;
}

